import * as React from "react";
import {Button, Modal, Form, Message, Ref} from 'semantic-ui-react'
import {observer} from "mobx-react";
import {rootStore} from "../stores";
import {Api, TrackerError} from "../../api";
import {Store} from "../../models";
import * as moment from 'moment';
import {removeModal} from "../../utils";

@observer
export class CreateInvitation extends React.Component <any, any> {
    state = {
        form: {
            contractor: "",
            currency: "eur",
            rate: "",
            loading: false,
            weekly_limit: "",
            error: null
        }
    };
    formElement: any;


    constructor(props: any) {
        super(props);

    }

    render() {
        const queryParams: any = rootStore.routerStore.routerState.queryParams;

        return (<Modal
            open={queryParams.modal == "create_invitation"}
            centered={false} dimmer={"blurring"}>
            <Modal.Header>Create Invitation</Modal.Header>
            <Modal.Content>
                <Form>
                    <Form.Select label='Choice project'
                                 options={Store.projects_as_options()}
                                 placeholder='Project'
                                 value={queryParams.modal_project_id}
                                 onChange={(e: any, data: any) => {
                                     rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                         rootStore.routerStore.routerState.params,
                                         {
                                             ...rootStore.routerStore.routerState.queryParams,
                                             modal_project_id: data.value
                                         });
                                 }}
                                 required/>
                    <Form.Group>
                        <Form.Select label='Currency'
                                     options={
                                         [{key: "eur", text: "EURO", value: "eur"}, {
                                             key: "usd",
                                             text: "USD",
                                             value: "usd"
                                         }]
                                     }
                                     placeholder='Currency'
                                     value={this.state.form.currency}
                                     onChange={(e: any, data: any) => {
                                         this.setState({
                                             form: {
                                                 ...this.state.form,
                                                 currency: data.value
                                             }
                                         })
                                     }}
                                     required/>
                        <Form.Input label='Price per hour'
                                    type={"number"}
                                    placeholder='Rate'
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                rate: data.value
                                            }
                                        })
                                    }}
                                    value={this.state.form.rate}
                                    required/>
                        <Form.Input label='Weekly limit'
                                    type={"number"}
                                    placeholder='Weekly limit'
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                weekly_limit: data.value
                                            }
                                        })
                                    }}
                                    value={this.state.form.weekly_limit}
                                    required/>
                    </Form.Group>

                    <Form.Input label='Username / Email'
                                placeholder='Username / Email'
                                onChange={(e: any, data: any) => {
                                    this.setState({
                                        form: {
                                            ...this.state.form,
                                            contractor: data.value
                                        }
                                    })
                                }}
                                value={this.state.form.contractor}
                                required/>
                </Form>

                {this.state.form.error ? <Message color='red'>{this.state.form.error}</Message> : null}
            </Modal.Content>
            <Modal.Actions>
                <Button color='black' onClick={() => {
                    rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                        rootStore.routerStore.routerState.params,
                        removeModal(rootStore.routerStore.routerState.queryParams))
                }}>
                    Cancel
                </Button>
                <Button
                    positive
                    icon='checkmark'
                    labelPosition='right'
                    content="Send Invite"
                    onClick={(e) => {
                        e.preventDefault();
                        this.setState({
                            form: {
                                ...this.state.form,
                                loading: true
                            }
                        });

                        Api.invitations.create(queryParams.modal_project_id,
                            this.state.form.contractor,
                            this.state.form.currency,
                            this.state.form.rate || "0",
                            this.state.form.weekly_limit || "0").then((response) => {

                            Store.add_invitation({
                                id: `${response.id}`,
                                created_at: moment(response.created_date_time).toDate(),
                                status: response.status,
                                project: `${response.project_id}`,
                                invitee: `${response.invitee_id}`,
                                terms: `${response.terms_id}`
                            });

                            this.setState({
                                form: {
                                    ...this.state.form,
                                    loading: false
                                }
                            });

                            rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                rootStore.routerStore.routerState.params,
                                removeModal(rootStore.routerStore.routerState.queryParams))
                        }).catch((error: TrackerError) => {
                            this.setState({
                                form: {
                                    ...this.state.form,
                                    error: error.message,
                                    loading: false
                                }
                            })
                        });
                        this.setState({
                            form: {
                                contractor: "",
                                currency: "eur",
                                rate: "",
                                loading: false,
                                weekly_limit: "",
                                error: null
                            }
                        })
                    }}
                />
            </Modal.Actions>
        </Modal>)
    }
}