import axios, {AxiosAdapter, AxiosError, AxiosResponse} from 'axios';
import {ContractState, InvitationStatus} from "./enumerations";
import {throttleAdapterEnhancer} from 'axios-extensions';

const http = axios.create({
    adapter: throttleAdapterEnhancer(axios.defaults.adapter as AxiosAdapter, {threshold: 20 * 1000})
});

export interface Axios<T> {
    data: T;
}

type UserType = "work" | "hire";

export interface IApiUser {
    email: string,
    id: string,
    is_admin: boolean,
    created_date_time: string,
    user_type: UserType
}

export interface IApiProject {
    id: number,
    created_date_time: number,
    owner_id: number,
    title: string,
    description: string

}

export interface IApiInvitation {
    id: string,
    created_date_time: string,
    status: InvitationStatus,
    project_id: string,
    invitee_id: string,
    invitee_email: string,
    terms_id: string

}

export interface IApiTerm {
    id: string,
    created_date_time: string,
    currency_id: "eur" | "usd"
    price_per_hour: number
    weekly_limit_in_hours: number
}

export interface IApiContract {
    id: string
    created_date_time: string
    status: ContractState
    project_id: string
    contractor_id: string
    terms_id: string
}

export interface TrackerError {
    code: number
    message: string
}

export interface IAuth {
    email: string,
    token: string,
    id: string
}

export interface IApiTotals {
    last_24_hours: number,
    current_week: number,
    previous_week: number,
    since_start: number
}

interface Options {
    url: string,
    jwt_token?: string,
    user_id?: string
    email?: string
}

export interface IApiEvent {
    id: number,
    created_date_time: string,
    contract_id: number,
    event_type: "log",
    keyboard_events_count: number,
    mouse_events_count: number,
    screenshot_url: string,
    title: string

}

class Endpoint {
    protected options: Options;

    constructor(options: Options) {
        this.options = options
    }

    protected headers(): any {
        let headers: any = {
            'Content-Type': 'application/json',
        };

        if (this.options.jwt_token) {
            headers['Authorization'] = `Bearer ${this.options.jwt_token}`
        }
        return headers
    }

    protected async post_request(path: string, input?: any): Promise<any> {
        let p = axios.post(`${this.options.url}${path}`,
            JSON.stringify(input),
            {
                headers: this.headers()
            }).then((r: AxiosResponse) => {
            return Promise.resolve(r.data)
        }).catch((error: AxiosError) => {
            if (error && error.response && error.response.data) {
                return Promise.reject(error.response!.data)
            }
            return Promise.reject({code: 5000, message: "network issues"})

        });
        return p
    }

    protected async get_request(path: string): Promise<any> {
        let p = axios.get(`${this.options.url}${path}`,
            {
                headers: this.headers()
            }).then((r: AxiosResponse) => {
            return Promise.resolve(r.data)
        }).catch((error: AxiosError) => {
            if (error && error.response && error.response.data) {
                return Promise.reject(error.response!.data)
            }
            return Promise.reject({code: 5000, message: "network issues"})

        });
        return p
    }

    protected async delete_request(path: string): Promise<any> {
        let p = axios.delete(`${this.options.url}${path}`,
            {
                headers: this.headers()
            }).then((r: AxiosResponse) => {
            return Promise.resolve(r.data)
        }).catch((error: AxiosError) => {
            if (error && error.response && error.response.data) {
                return Promise.reject(error.response!.data)
            }
            return Promise.reject({code: 5000, message: "network issues"})

        });
        return p
    }
}


class UsersEndpoint extends Endpoint {
    async register(email: string, username: string, user_type: "work" | "hire", password: string, first_name: string, last_name: string): Promise<IAuth> {
        return this.post_request("/users", {
            email: email,
            id: username,
            user_type: user_type,
            password: password,
            first_name: first_name,
            last_name: last_name
        })
    }

    async login(email: string, password: string): Promise<IAuth> {
        return this.post_request("/user/login", {
            email: email,
            password: password
        })
    }

    async get(id: string): Promise<IApiUser> {
        return this.get_request(`/users/${id}`)
    }
}

class ProjectsEndpoint extends Endpoint {
    async create(title: string, description: string): Promise<IApiProject> {
        return this.post_request("/projects", {
            title: title,
            description: description
        })
    }

    async list(): Promise<IApiProject[]> {
        return this.get_request(`/projects`)
    }

    async get(id: string): Promise<IApiProject> {
        return this.get_request(`/projects/${id}`)
    }

}

class InvitationsEndpoint extends Endpoint {
    async create(project_id: string, invitee_id: string, currency_id: string, price_per_hour: string, weekly_limit_in_hours: string): Promise<IApiInvitation> {
        return this.post_request("/invitations", {
            project_id: parseInt(project_id),
            invitee_id: invitee_id,
            currency_id: currency_id,
            price_per_hour: parseInt(price_per_hour),
            weekly_limit_in_hours: parseInt(weekly_limit_in_hours)
        })
    }

    async list(): Promise<IApiInvitation[]> {
        return this.get_request(`/invitations`)
    }

    async delete(id: string): Promise<boolean> {
        return this.delete_request(`/invitations/${id}`)
    }

    async accept(id: string): Promise<{ contract_id: number }> {
        return this.post_request(`/invitations/${id}/accept`)
    }

    async decline(id: string): Promise<boolean> {
        return this.post_request(`/invitations/${id}/decline`)
    }

    async get(id: string): Promise<IApiInvitation> {
        return this.get_request(`/invitations/${id}`)
    }
}


class ContractsEndpoint extends Endpoint {

    async resume(id: string): Promise<boolean> {
        return this.post_request(`/contracts/${id}/resume`)

    }

    async pause(id: string): Promise<boolean> {
        return this.post_request(`/contracts/${id}/pause`)
    }

    async end(id: string): Promise<boolean> {
        return this.post_request(`/contracts/${id}/end`)
    }

    async list(): Promise<IApiContract[]> {
        return this.get_request(`/contracts`)
    }

    async get(id: string): Promise<IApiContract> {
        return this.get_request(`/contracts/${id}`)
    }

    async terms(id: string): Promise<IApiTerm> {
        return this.get_request(`/contracts/${id}/terms`)
    }

    async totals(id: string): Promise<IApiTotals> {
        return this.get_request(`/contracts/${id}/totals`)
    }

    async events(id: string, from: string, to: string): Promise<IApiEvent[]> {
        return this.get_request(`/contracts/${id}/events?from=${from}&to=${to}`)
    }
}


class ApiConstructor {
    users: UsersEndpoint;
    projects: ProjectsEndpoint;
    invitations: InvitationsEndpoint;
    contracts: ContractsEndpoint;

    protected options: Options;
    protected reload: { (id: string): void };

    constructor(url: string) {
        this.options = {url: url};
        this.users = new UsersEndpoint(this.options);
        this.projects = new ProjectsEndpoint(this.options);
        this.invitations = new InvitationsEndpoint(this.options);
        this.contracts = new ContractsEndpoint(this.options);
        this.reload = (id: string) => {
            throw "Reload function not defined"
        }
    }

    set_reload(f: { (id: string): void }) {
        this.reload = f
    }

    login(email: string, id: string, token: string, save = true) {
        this.options.user_id = id;
        this.options.jwt_token = token;
        this.options.email = email;
        if (window && window.localStorage && save) {
            window.localStorage.setItem("user_id", id);
            window.localStorage.setItem("token", token);
            window.localStorage.setItem("email", email);
        }
        this.reload(id)
    }

    is_logged(): boolean {
        return !(!this.options.user_id || !this.options.jwt_token);
    }

    user_id(): string {
        return this.options.user_id!
    }


    logout() {
        delete this.options.user_id;
        delete this.options.jwt_token;
        delete this.options.email;

        if (window && window.localStorage) {
            window.localStorage.clear();
        }
    }
}

export const Api = new ApiConstructor("http://api.easywork.me");