import * as React from "react";
import {observer} from "mobx-react";
import {
    Label,
    Image,
    Icon,
    Container,
    Menu,
    Dropdown
} from 'semantic-ui-react'
import {Store} from "../../models";
import {rootStore} from "../stores";
import {Api} from "../../api";
import {CreateInvitation} from "../components/create-invitation";
import {ProjectStatus, UserKind} from "../../enumerations";
import {EndContract} from "../components/end-contract";

@observer
export class MainLayout extends React.Component <any, any> {

    render() {

        let trigger = (<Label image size={"large"}>
            <Image avatar spaced={"right"} src={(Store.user && Store.user!.avatar)}/>
            &nbsp;{(Store.user && Store.user!.username) || "user"}&nbsp;&nbsp;
            <Icon name='caret down'/>
        </Label>);
        return (
            <Container className={"main_layout"}>
                <CreateInvitation/>
                <EndContract/>
                <Menu secondary className={"main_layout_menu"}>
                    {Store.user && Store.user.kind == UserKind.client ? <Menu.Item as="a" name='projects'
                                                                                   active={rootStore.routerStore.routerState.routeName == "projects"}
                                                                                   onClick={() => {
                                                                                       rootStore.routerStore.goTo("projects", {}, {status: ProjectStatus.active})

                                                                                   }}/> : null}
                    <Menu.Item as="a" name='contracts'
                               active={rootStore.routerStore.routerState.routeName == "contracts"}
                               onClick={() => {
                                   rootStore.routerStore.goTo("contracts", {}, {status: "contract_started"})

                               }}/>
                    <Menu.Menu position='right'>
                        <Dropdown trigger={trigger} pointing='top left' icon={null}>
                            <Dropdown.Menu>
                                <Dropdown.Item text='Profile' icon={"user circle"}/>
                                <Dropdown.Item text='Logout' icon={"angle left"} onClick={() => {
                                    Api.logout();
                                    rootStore.routerStore.goTo("login")
                                }}/>
                            </Dropdown.Menu>
                        </Dropdown>
                    </Menu.Menu>
                </Menu>
                {this.props.children}
            </Container>
        )
    }

}


