import * as React from "react";
import {ContractState, InvitationStatus, RateCurrency} from "./enumerations";
import {Label} from "semantic-ui-react";

export function mapCurrency(c: RateCurrency) {
    switch (c) {
        case RateCurrency.euro:
            return "€";
        case RateCurrency.dollar:
            return "$";
    }
    return "€"
}

export function mapInvitationStatus(c: InvitationStatus) {
    switch (c) {
        case InvitationStatus.pending:
            return "sent invite";
    }
    return c
}


export function mapStatusToLabel(c: ContractState) {

    switch (c) {
        case ContractState.started:
            return <Label size={"tiny"} color={"blue"}>Active</Label>;
        case ContractState.not_started:
            return <Label size={"tiny"} color={"blue"}>Active</Label>;
        case ContractState.paused:
            return <Label size={"tiny"}>Paused</Label>;
        case ContractState.ended:
            return <Label size={"tiny"} color={"black"}>Ended</Label>;
    }
}


export function stripString(str: string, max: number): string {
    if (!str) {
        return ""
    }
    if (str.length > max) {
        return str.substring(0, max) + "..."
    }
    return str
}

export function removeModal(obj: any): any {
    let ret: any = {};
    for (let key of Object.keys(obj)) {
        if (!key.startsWith("modal")) {
            ret[key] = obj[key]
        }
    }
    return ret;
}

export function getHours(hour: number): string {
    hour = hour / 60;

    return `${Math.floor(hour)}`
}

export function getMinutes(hour: number): string {
    hour = hour / 60;

    const minutes = Math.ceil((hour - Math.floor(hour)) * 60);
    if (minutes < 10) {
        return `0${minutes}`
    }
    return `${minutes}`
}
