import * as React from "react";
import {observer} from "mobx-react";
import {
    Container,
    Dropdown,
    Segment,
    Header,
    Table,
    Icon,
    Button,
    Input, Label, Popup,
} from 'semantic-ui-react'
import {Store} from "../../models"
import * as moment from 'moment';
import {rootStore} from "../stores";
import {CreateProject} from "../components/create-project";
import {ContractState, InvitationStatus, ProjectStatus, UserKind} from "../../enumerations";
import {IProject} from "../../models/interfaces";
import {RouterLink} from "mobx-state-router";
import {Contracts} from "./contracts";

interface IContractListProps {
    projects: IProject[],
}

@observer
class ProjectsList extends React.Component <IContractListProps, any> {
    render() {

        return <Table padded className={"site-table"}>
            <Table.Header>
                <Table.Row>
                    <Table.HeaderCell style={{width: 64}}/>
                    <Table.HeaderCell>Name</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 160}}>Created</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 192}}>Contracts</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 64}}>Status</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 64}}/>
                </Table.Row>
            </Table.Header>

            <Table.Body>
                {this.props.projects.map((project) => {
                    return <Table.Row key={project.id}>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("project", {
                                project_id: project.id,
                                view: "description"
                            })
                        }}>
                            <Icon name={"list alternate"} size={"big"}/>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("project", {
                                project_id: project.id,
                                view: "description"
                            })
                        }}>
                            <h5> {project.name}</h5>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("project", {
                                project_id: project.id,
                                view: "description"
                            })
                        }}>
                            <h5>{moment(project.created_at).fromNow()}</h5>
                        </Table.Cell>
                        <Table.Cell>
                            {Store.filter_contracts(ContractState.started, "", project.id).length > 0 ?
                                <Popup inverted
                                       trigger={
                                           <Label circular
                                                  onClick={() => {
                                                      rootStore.routerStore.goTo("contracts", {}, {
                                                          project_id: project.id,
                                                          status: "contract_started"
                                                      })
                                                  }}
                                                  color={"blue"}>
                                               {Store.filter_contracts(ContractState.started, "", project.id).length}
                                           </Label>}
                                       content='Active'/> : null}
                            {Store.filter_invitations(InvitationStatus.pending, "", project.id).length > 0 ?
                                <Popup inverted
                                       trigger={
                                           <Label circular
                                                  onClick={() => {
                                                      rootStore.routerStore.goTo("contracts", {}, {
                                                          project_id: project.id,
                                                          status: "invitation_pending"
                                                      })
                                                  }}
                                                  color={"orange"}>
                                               {Store.filter_invitations(InvitationStatus.pending, "", project.id).length}
                                           </Label>
                                       } content='Sent Invite'/> : null}
                            {Store.filter_contracts(ContractState.paused, "", project.id).length > 0 ?
                                <Popup inverted
                                       trigger={
                                           <Label circular
                                                  onClick={() => {
                                                      rootStore.routerStore.goTo("contracts", {}, {
                                                          project_id: project.id,
                                                          status: "contract_paused"
                                                      })
                                                  }}>
                                               {Store.filter_contracts(ContractState.paused, "", project.id).length}
                                           </Label>}
                                       content='Paused'/> : null}
                            {Store.filter_contracts(ContractState.ended, "", project.id).length > 0 ?
                                <Popup inverted
                                       trigger={
                                           <Label circular
                                                  onClick={() => {
                                                      rootStore.routerStore.goTo("contracts", {}, {
                                                          project_id: project.id,
                                                          status: "contract_ended"
                                                      })
                                                  }}
                                                  color={"black"}>
                                               {Store.filter_contracts(ContractState.ended, "", project.id).length}
                                           </Label>}
                                       content='Ended'/> : null}
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("project", {
                                project_id: project.id,
                                view: "description"
                            })
                        }}>
                            {project.status == ProjectStatus.active ?
                                <Label color={"blue"} size={"tiny"}>Active</Label> :
                                <Label size={"tiny"}>Archived</Label>}
                        </Table.Cell>
                        <Table.Cell>
                            <Dropdown icon='ellipsis horizontal' className={"grey-icon"} direction={"left"}>
                                <Dropdown.Menu>
                                    <Dropdown.Item onClick={() => {
                                        rootStore.routerStore.goTo("project", {
                                            project_id: project.id,
                                            view: "description"
                                        })
                                    }
                                    }>
                                        <Icon name='list alternate outline'/>
                                        <span className='text'>view</span>
                                    </Dropdown.Item>
                                    <Dropdown.Item onClick={() => {
                                        rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                            rootStore.routerStore.routerState.params,
                                            {
                                                ...rootStore.routerStore.routerState.queryParams,
                                                modal: "create_invitation",
                                                modal_project_id: project.id
                                            });

                                    }}>
                                        <Icon name='envelope'/>
                                        <span className='text'>invite user</span>
                                    </Dropdown.Item>
                                    <Dropdown.Item>
                                        <Icon name='archive'/>
                                        <span className='text'>archive</span>
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>

                        </Table.Cell>
                    </Table.Row>

                })}

            </Table.Body>
        </Table>
    }
}

@observer
export class Projects extends React.Component <any, any> {
    constructor(props: any) {
        super(props);
        this.state = {search_term: ""};

    }

    changeTab = (e: any, data: any) => {
        const param: any = rootStore.routerStore.routerState.queryParams;
        rootStore.routerStore.goTo("contracts", {}, {page: param.page, status: data.name});
        this.setState({search_term: ""})
    };

    render() {
        const queryParams: any = rootStore.routerStore.routerState.queryParams;
        if (Store.user && Store.user.kind != UserKind.client) {
            return null;
        }
        return (
            <Container fluid className={"project-list"}>
                <CreateProject/>
                <Segment basic className={"project-header"}>
                    <div className={"project-header-left"}>
                        <Header as={"h2"}>Projects</Header>
                    </div>
                    <div className={"project-header-right"}>
                    </div>
                </Segment>
                <Segment basic className={"project-header"}>
                    <div className={"project-header-left"}>
                        <Input icon='search'
                               placeholder='Search projects'
                               value={this.state.search_term}
                               onChange={(e: any, data: any) => {
                                   this.setState({search_term: data.value})
                               }}/></div>
                    <div className={"project-header-right"}>
                        <Button icon
                                labelPosition='left'
                                size={"tiny"}
                                onClick={() => {
                                    rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                        {},
                                        {modal: "create_project"});
                                }}
                                primary>
                            <Icon name={"plus"}/>
                            ADD PROJECT
                        </Button>
                    </div>

                </Segment>

                <Segment basic style={{padding: 0}}>
                    <div className={"table-filters"}>
                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == ProjectStatus.active ? "filter-active" : ""}
                                routeName={"projects"}
                                queryParams={{status: ProjectStatus.active}}>
                                Active({Store.filter_projects(ProjectStatus.active).length})
                            </RouterLink>
                        </div>
                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == ProjectStatus.archived ? "filter-active" : ""}
                                routeName={"projects"}
                                queryParams={{status: ProjectStatus.archived}}>
                                Archived({Store.filter_projects(ProjectStatus.archived).length})
                            </RouterLink>
                        </div>
                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == ProjectStatus.none ? "filter-active" : ""}
                                routeName={"projects"}
                                queryParams={{status: ProjectStatus.none}}>
                                All({Store.filter_projects(ProjectStatus.none).length})
                            </RouterLink>
                        </div>
                    </div>
                    <ProjectsList
                        projects={Store.filter_projects(queryParams.status || ProjectStatus.none, this.state.search_term)}/>
                </Segment>


            </Container>
        )
    }

}


