import * as React from "react";
import {observer} from "mobx-react";
import {
    Segment,
    Header,
    Form,
    Message,
    Button,
} from 'semantic-ui-react'
import {RouterLink} from 'mobx-state-router';
import {Api, IAuth, TrackerError} from "../../api";
import {rootStore} from "../stores";
import {ContractState} from "../../enumerations";


@observer
export class Login extends React.Component <any, any> {
    state = {
        form: {
            email: "",
            password: "",
            error: null,
            loading: false
        }
    };

    render() {
        return (
            <Segment>
                <Segment vertical>
                    <Header>Please sign in</Header>
                </Segment>
                <Segment basic>
                    <Form loading={this.state.form.loading} className={"login-form"} onSubmit={(e: any) => {
                        this.setState({
                            form: {
                                ...this.state.form,
                                loading: true
                            }
                        });
                        Api.users.login(this.state.form.email, this.state.form.password).then((response) => {
                            Api.login(response.email, response.id, response.token);
                            this.setState({
                                form: {
                                    ...this.state.form,
                                    loading: false
                                }
                            });
                            rootStore.routerStore.goTo("contracts", {}, {status: `contract_${ContractState.started}`});

                        }).catch((error: TrackerError) => {
                            this.setState({
                                form: {
                                    ...this.state.form,
                                    error: error.message,
                                    loading: false
                                }
                            })
                        });
                        e.preventDefault();
                    }}>
                        <Form.Input label='Email address'
                                    placeholder='Enter email'
                                    type="email"
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                email: data.value
                                            }
                                        })
                                    }} value={this.state.form.email}
                                    required/>
                        <Form.Input label='Password'
                                    placeholder='Password'
                                    type="password"
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                password: data.value
                                            }
                                        })
                                    }} value={this.state.form.password}
                                    required/>
                        Have no account? Please <RouterLink routeName={"register"}>register now!</RouterLink>
                        {this.state.form.error ? <Message color='red'>{this.state.form.error}</Message> : null}

                        <div className={"login-form-footer"}>
                            <Button loading={this.state.form.loading} size={"big"} primary>Sign in</Button>
                        </div>
                    </Form>
                </Segment>
            </Segment>
        )
    }

}


