import * as React from "react";
import {observer} from "mobx-react";
import {Label, Icon, Button, Segment, Popup, Header, Modal} from 'semantic-ui-react'
import * as moment from 'moment';
import {Store} from "../../../models";
import {rootStore} from "../../stores";
import DayPicker from 'react-day-picker'
import {ILog} from "../../../models/interfaces";
import {ScreenShot} from "./screenshot";
import {removeModal} from "../../../utils";


type workDayLogs = { hour: Date, logs: ILog[] };
const shots = [0, 1, 2, 3, 4, 5];


@observer
class LogDetail extends React.Component <{ log: ILog }, any> {
    render() {
        return (
            <div className={"log-detail"}>
                <img src={this.props.log.screen_shot}/>
                <Segment basic>
                    <div className={"log-detail-item"}>
                        <Label>Task Title</Label>
                        &nbsp;{this.props.log.task_title}</div>
                    <div className={"log-detail-item"}>
                        <Label>Screenshot Taken</Label>
                        &nbsp;{moment(this.props.log.created_at).format("h:mm A").toLowerCase()}</div>
                    <div className={"log-detail-item"}>
                        <Label>Keyboard Events</Label>
                        &nbsp;{this.props.log.keyboard_count}</div>
                    <div className={"log-detail-item"}>
                        <Label>Mouse Events</Label>
                        &nbsp;{this.props.log.mouse_count}</div>
                </Segment>

            </div>
        )
    }
}

@observer
export class WorkDiary extends React.Component <any, any> {

    constructor(props: any) {
        super(props);
    }

    handleDayClick = (day: Date, modifiers: Object, e: any) => {
        rootStore.routerStore.goTo(
            rootStore.routerStore.routerState.routeName,
            rootStore.routerStore.routerState.params,
            {
                ...rootStore.routerStore.routerState.queryParams,
                date: moment(day).format("YYYYMMDD")
            }
        )
    }

    render() {
        let queryParams: any = rootStore.routerStore.routerState.queryParams;
        let params: any = rootStore.routerStore.routerState.params;
        let date = moment();
        if (queryParams.date) {
            date = moment(queryParams.date || moment().format("YYYYMMDD"), "YYYYMMDD");
        }
        let contract_id = params.contract_id;
        let contract = Store.contracts.get(contract_id);
        const workDays: workDayLogs[] = Store.get_work_diary(date.toDate(), contract_id);

        return (
            <div>
                <Modal open={queryParams.modal == "shot_modal"} closeIcon closeOnDimmerClick onClose={()=>{
                    rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                        rootStore.routerStore.routerState.params,
                        removeModal(rootStore.routerStore.routerState.queryParams))
                }}>
                    <Modal.Content image>
                        <ScreenShot/>
                    </Modal.Content>
                </Modal>
                <Segment vertical className={"work-diary-options"}>
                    <Button icon={"caret left"}
                            onClick={() => {
                                rootStore.routerStore.goTo(
                                    rootStore.routerStore.routerState.routeName,
                                    rootStore.routerStore.routerState.params,
                                    {
                                        ...rootStore.routerStore.routerState.queryParams,
                                        date: date.add(-1, "days").format("YYYYMMDD")
                                    }
                                )
                            }}
                            disabled={date.isSameOrBefore(moment(contract && contract.started_at || ""))}
                            color={date.isSameOrBefore(moment(contract && contract.started_at || "")) ? "grey" : "blue"}/>

                    <Popup
                        trigger={<Label color={"blue"} size={"large"} as={"a"}><Icon
                            name='calendar alternate'/>{date.format("ddd, MMMM D, YYYY")}</Label>}
                        content={<DayPicker selectedDays={date.toDate()}
                                            fromMonth={(contract && contract.started_at) || moment().toDate()}
                                            toMonth={moment().toDate()} onDayClick={this.handleDayClick}
                                            disabledDays={[{
                                                after: moment(contract && contract.started_at || "").toDate(),
                                                before: moment().toDate()
                                            }]}/>}
                        on='click'
                        position='bottom right'
                    />

                    <Button disabled={date.isSameOrAfter(moment(), "day")}
                            onClick={() => {
                                rootStore.routerStore.goTo(
                                    rootStore.routerStore.routerState.routeName,
                                    rootStore.routerStore.routerState.params,
                                    {
                                        ...rootStore.routerStore.routerState.queryParams,
                                        date: date.add(1, "days").format("YYYYMMDD")
                                    }
                                )
                            }}
                            color={date.isSameOrAfter(moment(), "day") ? "grey" : "blue"} icon={"caret right"}/>
                    <Button disabled={date.isSameOrAfter(moment(), "day")}
                            color={date.isSameOrAfter(moment(), "day") ? "grey" : "blue"}
                            onClick={() => {
                                rootStore.routerStore.goTo(
                                    rootStore.routerStore.routerState.routeName,
                                    rootStore.routerStore.routerState.params,
                                    {
                                        ...rootStore.routerStore.routerState.queryParams,
                                        date: moment().format("YYYYMMDD")
                                    }
                                )
                            }}>TODAY</Button>

                </Segment>
                {workDays.length == 0 ? <Segment inverted color={"grey"} padded={"very"} textAlign={"center"}>
                    <Header>No data for this date</Header>
                </Segment> : <Segment basic>{workDays.map((wd: workDayLogs) => {
                    let hour = moment(wd.hour).format("h");

                    return <div className={"work-diary-row"} key={moment(wd.hour).format("hA")}>
                        <div className={"work-diary-hour"}>
                            <div className={"work-diary-hour-display"}>
                                <span>{hour}</span>
                                <span>{moment(wd.hour).format("A").toLowerCase()}</span>
                            </div>
                        </div>
                        <div className={"work-diary-shot-list"}>
                            {shots.map((shot_number) => {
                                return <div key={shot_number} className={"work-diary-shot"}>
                                    {wd.logs[shot_number] ? [<Popup key={"1"}
                                                                    trigger={<img src={wd.logs[shot_number].screen_shot}
                                                                                  style={{cursor: "pointer"}}
                                                                                  onClick={() => {
                                                                                      rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                                                                          rootStore.routerStore.routerState.params,
                                                                                          {
                                                                                              ...rootStore.routerStore.routerState.queryParams,
                                                                                              modal: "shot_modal",
                                                                                              shot_id: wd.logs[shot_number].id
                                                                                          }
                                                                                      )
                                                                                  }}/>}
                                                                    content={<LogDetail log={wd.logs[shot_number]}/>}
                                                                    on='hover'
                                                                    position='top center'
                                        />,
                                            <div
                                                key={"2"}> {moment(wd.logs[shot_number].created_at).format("h:mm A").toLowerCase()}</div>] :
                                        <div className={"wds-placeholder"}/>}
                                </div>
                            })}

                        </div>
                    </div>
                })} </Segment>
                }

            </div>
        )
    }

}


