import * as React from "react";
import {observer} from "mobx-react";
import {
    Container,
    Dropdown,
    Segment,
    Header,
    Table,
    Icon,
    Button,
    Input,
    Label,
} from 'semantic-ui-react'
import {Store} from "../../models"
import * as moment from 'moment';
import {rootStore} from "../stores";
import {
    ContractState,
    InvitationStatus,
    ProjectStatus,
    RateCurrency,
    ResourceStatus,
    UserKind
} from "../../enumerations";
import {getHours, getMinutes, mapCurrency, mapStatusToLabel} from "../../utils";
import {RouterLink} from "mobx-state-router";
import {IContract, IContractTotal, IInvitation, IProject, ITerm, IUser} from "../../models/interfaces";
import {Api} from "../../api";

interface IContractListProps {
    contracts: IContract[],
    invitations: IInvitation[]
}


@observer
class ContractList extends React.Component <IContractListProps, any> {
    render() {
        return <Table padded className={"site-table"}>
            <Table.Header>
                <Table.Row>
                    <Table.HeaderCell style={{width: 64}}/>
                    <Table.HeaderCell style={{width: 160}}>
                        {Store.user && Store.user.kind == UserKind.client ? "Contractor" : "Client"}
                    </Table.HeaderCell>
                    <Table.HeaderCell>Project</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 160}}>Started</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 144}}>Progress</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 120}}>Status</Table.HeaderCell>
                    <Table.HeaderCell style={{width: 64}}/>
                </Table.Row>
            </Table.Header>

            <Table.Body>
                {this.props.contracts.map((contract) => {
                    const project = contract.project as IProject;
                    const contractor = contract.contractor as IUser;
                    const hirer = project && project.owner as IUser;
                    const terms: ITerm = (contract && contract.terms as ITerm) || {
                        rate_currency: RateCurrency.euro,
                        weekly_limit: 0,
                        rate: 0
                    };

                    const totals: IContractTotal = (contract && contract.totals as IContractTotal) || {
                        last_24_hours: 0,
                        current_week: 0,
                        previous_week: 0,
                        since_start: 0
                    };
                    return <Table.Row key={contract.id}>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("contract", {
                                contract_id: contract.id,
                                view: "time"
                            });
                        }}>
                            <Icon name={"address card"} size={"big"}/>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("contract", {
                                contract_id: contract.id,
                                view: "time"
                            });
                        }}>
                            {Store.user && Store.user.kind == UserKind.client ? <Label image>
                                <img src={contractor && contractor.avatar}/>
                                {contractor && contractor.username}
                            </Label> : <Label image>
                                <img src={hirer && hirer.avatar}/>
                                {hirer && hirer.username}
                            </Label>}
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("contract", {
                                contract_id: contract.id,
                                view: "time"
                            });
                        }}>
                            <h5>{project && project.name}</h5>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("contract", {
                                contract_id: contract.id,
                                view: "time"
                            });
                        }}>
                            <h5>{moment(contract.started_at).fromNow()}</h5>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("contract", {
                                contract_id: contract.id,
                                view: "time"
                            });
                        }}>
                            <b>{`${mapCurrency(terms.rate_currency || RateCurrency.euro)}${(totals.current_week/60 * (terms.rate || 0)).toFixed(2)}`}</b> | {`${mapCurrency(terms.rate_currency || RateCurrency.euro)}${terms.rate}`}<br/>
                            {`${getHours(totals.current_week)}:${getMinutes(totals.current_week)}`} of {terms.weekly_limit||0} hrs<br/>
                            this week*
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            rootStore.routerStore.goTo("contract", {
                                contract_id: contract.id,
                                view: "time"
                            });
                        }}>
                            {mapStatusToLabel(contract.status!)}
                        </Table.Cell>
                        <Table.Cell>
                            <Dropdown icon='ellipsis horizontal' className={"grey-icon"} simple item direction={"left"}>
                                <Dropdown.Menu>
                                    <Dropdown.Item onClick={() => {
                                        rootStore.routerStore.goTo("contract", {
                                            contract_id: contract.id,
                                            view: "time"
                                        });
                                    }}>
                                        <Icon name='list alternate outline'/>
                                        <span className='text'>View</span>
                                    </Dropdown.Item>
                                    {(contract.status == ContractState.not_started || contract.status == ContractState.started || contract.status == ContractState.paused) && (Store.user && Store.user.kind == UserKind.client) ?
                                        <Dropdown.Item onClick={() => {
                                            if (contract.status == ContractState.paused) {
                                                Api.contracts.resume(contract.id).then(() => {
                                                    contract.resume!()
                                                })
                                            }
                                            else {
                                                Api.contracts.pause(contract.id).then(() => {
                                                    contract.pause!()
                                                })
                                            }
                                        }}>
                                            <Icon name={contract.status == ContractState.paused ? "play" : "pause"}/>
                                            <span
                                                className='text'>{contract.status == ContractState.paused ? "Resume" : "Pause"}</span>
                                        </Dropdown.Item> : null}
                                    {contract.status == ContractState.ended ? null :
                                        <Dropdown.Item onClick={() => {
                                            rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                                rootStore.routerStore.routerState.params,
                                                {
                                                    ...rootStore.routerStore.routerState.queryParams,
                                                    modal: "end_contract",
                                                    modal_contract_id: contract.id
                                                });
                                        }}>
                                            <Icon name='close'/>
                                            <span className='text'>End</span>
                                        </Dropdown.Item>}
                                </Dropdown.Menu>
                            </Dropdown>

                        </Table.Cell>
                    </Table.Row>

                })}
                {this.props.invitations.map((invitation) => {
                    const invitee = invitation.invitee as IUser;
                    const project = invitation.project as IProject;

                    return <Table.Row key={invitation.id}>
                        <Table.Cell onClick={() => {
                            if (Store.user && Store.user.kind == UserKind.contractor) {
                                rootStore.routerStore.goTo("project", {
                                    project_id: project.id,
                                    view: "description"
                                });
                            }

                        }}>
                            <Icon name={"paper plane"} size={"big"}/>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            if (Store.user && Store.user.kind == UserKind.contractor) {
                                rootStore.routerStore.goTo("project", {
                                    project_id: project.id,
                                    view: "description"
                                });
                            }

                        }}>
                            <Label image>
                                <img src={invitee && invitee.avatar}/>
                                {invitee && invitee.username}
                            </Label>

                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            if (Store.user && Store.user.kind == UserKind.contractor) {
                                rootStore.routerStore.goTo("project", {
                                    project_id: project.id,
                                    view: "description"
                                });
                            }

                        }}>
                            <h5>{project && project.name}</h5>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            if (Store.user && Store.user.kind == UserKind.contractor) {
                                rootStore.routerStore.goTo("project", {
                                    project_id: project.id,
                                    view: "description"
                                });
                            }

                        }}>
                            <h5>{moment(invitation.created_at).fromNow()}</h5>
                        </Table.Cell>
                        <Table.Cell onClick={() => {
                            if (Store.user && Store.user.kind == UserKind.contractor) {
                                rootStore.routerStore.goTo("project", {
                                    project_id: project.id,
                                    view: "description"
                                });
                            }

                        }}>---</Table.Cell>
                        <Table.Cell onClick={() => {
                            if (Store.user && Store.user.kind == UserKind.contractor) {
                                rootStore.routerStore.goTo("project", {
                                    project_id: project.id,
                                    view: "description"
                                });
                            }

                        }}>
                            <Label size={"tiny"}
                                   color={"orange"}>{Store.user && Store.user.kind == UserKind.client ? "Sent Invite" : "Received Invite"}</Label>
                        </Table.Cell>
                        <Table.Cell>
                            <Dropdown icon='ellipsis horizontal' className={"grey-icon"} direction={"left"}>
                                <Dropdown.Menu>
                                    {Store.user && Store.user.kind == UserKind.contractor ?
                                        <Dropdown.Item onClick={() => {
                                            rootStore.routerStore.goTo("project",
                                                {project_id: project.id, view: "description"})
                                        }}>
                                            <Icon name='list alternate outline'/>
                                            <span className='text'>View</span>
                                        </Dropdown.Item> : null}
                                    {Store.user && Store.user.kind == UserKind.contractor ?
                                        <Dropdown.Item onClick={() => {
                                            Api.invitations.accept(invitation.id).then((response) => {
                                                Store.delete_invitation(invitation.id);
                                                Api.contracts.get(`${response.contract_id}`).then((contract) => {
                                                    Store.add_contract({
                                                        id: `${contract.id}`,
                                                        project: `${contract.project_id}`,
                                                        contractor: `${contract.contractor_id}`,
                                                        started_at: moment(contract.created_date_time).toDate(),
                                                        terms: `${contract.terms_id}`,
                                                        status: contract.status,
                                                        $resource_status: ResourceStatus.loaded
                                                    })
                                                })
                                            })
                                        }}>
                                            <Icon name='check'/>
                                            <span className='text'>Accept</span>
                                        </Dropdown.Item> : null}
                                    {Store.user && Store.user.kind == UserKind.contractor ?
                                        <Dropdown.Item onClick={() => {
                                            Api.invitations.decline(invitation.id).then((response) => {
                                                Store.delete_invitation(invitation.id);
                                            })
                                        }}>
                                            <Icon name='times circle'/>
                                            <span className='text'>Decline</span>
                                        </Dropdown.Item> : null}
                                    {Store.user && Store.user.kind == UserKind.client ?
                                        <Dropdown.Item onClick={() => {
                                            Api.invitations.delete(invitation.id).then((response) => {
                                                Store.delete_invitation(invitation.id)
                                            })
                                        }}>
                                            <Icon name='window close'/>
                                            <span className='text'>Cancel</span>
                                        </Dropdown.Item> : null}
                                </Dropdown.Menu>
                            </Dropdown>

                        </Table.Cell>
                    </Table.Row>

                })}
            </Table.Body>
        </Table>
    }
}


@observer
export class Contracts extends React.Component <any, any> {
    constructor(props: any) {
        super(props);
        this.state = {search_term: ""};

    }

    changeTab = (e: any, data: any) => {
        const param: any = rootStore.routerStore.routerState.queryParams;
        rootStore.routerStore.goTo("contracts", {}, {page: param.page, status: data.name});
        this.setState({search_term: ""})
    };

    render() {
        const queryParams: any = rootStore.routerStore.routerState.queryParams;
        let invitation_status = null;

        if (queryParams.status == `invitation_${InvitationStatus.pending}`) {
            invitation_status = InvitationStatus.pending
        } else if (queryParams.status == `none`) {
            invitation_status = InvitationStatus.none
        }

        let contract_status = null;

        if (queryParams.status == `contract_${ContractState.started}`) {
            contract_status = ContractState.started
        } else if (queryParams.status == `contract_${ContractState.paused}`) {
            contract_status = ContractState.paused
        } else if (queryParams.status == `contract_${ContractState.ended}`) {
            contract_status = ContractState.ended
        } else if (queryParams.status == `none`) {
            contract_status = ContractState.none
        }
        let picked_project = Store.projects.get(queryParams.project_id);
        return (
            <Container fluid className={"project-list"}>
                <Segment basic>
                    <Header as={"h2"}>Contracts </Header>
                    {picked_project ?
                        <Button icon
                                labelPosition='left'
                                size={"mini"}
                                onClick={() => {
                                    rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                        rootStore.routerStore.routerState.params,
                                        {
                                            ...rootStore.routerStore.routerState.queryParams,
                                            project_id: ""
                                        });
                                }}
                                primary>
                            <Icon name={"close"}/>
                            {picked_project.name}
                        </Button>
                        : null}
                </Segment>
                <Segment basic className={"project-header"}>
                    <div className={"project-header-left"}>
                        <Input icon='search'
                               placeholder='Search contracts'
                               value={this.state.search_term}
                               onChange={(e: any, data: any) => {
                                   this.setState({search_term: data.value})
                               }}/>
                    </div>
                    <div className={"project-header-right"}>
                        {(Store.user && Store.user.kind == UserKind.client) && Store.projects.size > 0 ?
                            <Button icon
                                    labelPosition='left'
                                    size={"tiny"}
                                    onClick={() => {
                                        rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                            rootStore.routerStore.routerState.params,
                                            {
                                                ...rootStore.routerStore.routerState.queryParams,
                                                modal: "create_invitation",
                                                modal_project_id: queryParams.project_id
                                            });
                                    }}
                                    primary>
                                <Icon name={"paper plane"}/>
                                INVITE USER
                            </Button> : null}
                    </div>

                </Segment>

                <Segment basic style={{padding: 0}}>
                    <div className={"table-filters"}>
                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == `contract_${ContractState.started}` ? "filter-active" : ""}
                                routeName={"contracts"}
                                queryParams={{...queryParams, status: `contract_${ContractState.started}`}}>
                                Active({Store.filter_contracts(ContractState.started, "", queryParams.project_id).length})
                            </RouterLink>
                        </div>

                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == `invitation_${InvitationStatus.pending}` ? "filter-active" : ""}
                                routeName={"contracts"}
                                queryParams={{...queryParams, status: `invitation_${InvitationStatus.pending}`}}>
                                {Store.user && Store.user.kind == UserKind.client ? "Sent Invite" : "Received Invite"}({Store.filter_invitations(InvitationStatus.pending, "", queryParams.project_id).length})
                            </RouterLink>
                        </div>
                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == `contract_${ContractState.paused}` ? "filter-active" : ""}
                                routeName={"contracts"}
                                queryParams={{...queryParams, status: `contract_${ContractState.paused}`}}>
                                Paused({Store.filter_contracts(ContractState.paused, "", queryParams.project_id).length})
                            </RouterLink>
                        </div>
                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == `contract_${ContractState.ended}` ? "filter-active" : ""}
                                routeName={"contracts"}
                                queryParams={{...queryParams, status: `contract_${ContractState.ended}`}}>
                                Ended({Store.filter_contracts(ContractState.ended, "", queryParams.project_id).length})
                            </RouterLink>
                        </div>
                        <div className={"filter"}>
                            <RouterLink
                                className={queryParams.status == "none" ? "filter-active" : ""}
                                routeName={"contracts"}
                                queryParams={{...queryParams, status: "none"}}>
                                All({Store.filter_contracts(ContractState.none, "", queryParams.project_id).length + Store.filter_invitations(InvitationStatus.none, "", queryParams.project_id).length})
                            </RouterLink>
                        </div>


                    </div>
                    <ContractList
                        contracts={Store.filter_contracts(contract_status, this.state.search_term, queryParams.project_id)}
                        invitations={Store.filter_invitations(invitation_status, this.state.search_term, queryParams.project_id)}
                    />
                </Segment>


            </Container>
        )
    }

}


