import {types, flow, IModelType} from "mobx-state-tree"
import * as moment from 'moment';
import {Project} from "./projects";
import {User} from "./users";
import {ContractState, InvitationStatus, ProjectStatus, ResourceStatus, UserKind} from "../enumerations";
import {IContract, IContractWeekReduce, IInvitation, ILog, IProject, ITerm, IUser} from "./interfaces";
import {Api, IApiContract, IApiEvent, IApiInvitation, IApiProject, IApiUser} from "../api";
import {Contract, ContractTerm} from "./contracts";
import {Invitation} from "./invitations";
import {generate_logs} from "../mocks";


/*
    logs models
 */

const Log: IModelType<Partial<ILog>, ILog> = types.model({
    id: types.identifier(types.string),
    screen_shot: types.optional(types.string, ""),
    created_at: types.Date,
    task_title: types.optional(types.string, ""),
    contract: types.reference(Contract),
    keyboard_count: types.optional(types.number, 0),
    mouse_count: types.optional(types.number, 0),
    previous_log_id: types.maybe(types.string),
    next_log_id: types.maybe(types.string),

});


const Root = types.model({
    list_project_date: types.maybe(types.Date),
    list_contract_date: types.maybe(types.Date),
    list_invitation_date: types.maybe(types.Date),
    list_interval: types.optional(types.number, 20),
    user: types.maybe(types.reference(User)),
    users: types.optional(types.map(User), {}),
    projects: types.optional(types.map(Project), {}),
    contracts: types.optional(types.map(Contract), {}),
    invitations: types.optional(types.map(Invitation), {}),
    terms: types.optional(types.map(ContractTerm), {}),
    logs: types.optional(types.map(Log), {}),


}).views(self => {
    const count_contracts = function (contract_state: ContractState) {
        let count = 0;
        for (const contract of Array.from(self.contracts.values())) {
            if (contract.status == contract_state) {
                count += 1;
            }
        }
        return count
    };
    return {count_contracts}
}).views(self => {
    const filter_contracts = function (status: ContractState | null, search_term: string = "", project_id: string = ""): IContract[] {
        let response: IContract[] = [];
        if (status == null) {
            return response
        }
        for (const contract of Array.from(self.contracts.values())) {
            let contract_status = contract.status;
            if (status == ContractState.started && contract_status == ContractState.not_started) {
                contract_status = ContractState.started
            }
            if (contract.$resource_status != ResourceStatus.loaded) {
                continue
            }
            if (project_id.trim() != "") {
                const project: IProject = contract.project as IProject;
                if (project.id != project_id) {
                    continue
                }
            }

            if (contract_status == status || status == ContractState.none) {
                if (search_term.trim() != "") {
                    const project: IProject = contract.project as IProject;
                    const contractor: IUser = contract.contractor as IUser;

                    if (project.name!.toLowerCase().includes(search_term.toLowerCase()) || contractor.username!.toLowerCase().includes(search_term.toLowerCase())) {
                        response.push(contract)
                    }
                    continue
                }
                response.push(contract)
            }
        }
        return response;
    };
    const filter_invitations = function (status: InvitationStatus | null, search_term: string = "", project_id: string = ""): IInvitation[] {

        let response: IInvitation[] = [];
        if (status == null) {
            return response
        }
        for (const invitation of Array.from(self.invitations.values())) {
            if (invitation.status == InvitationStatus.declined || invitation.status == InvitationStatus.accepted) {
                continue
            }
            if (invitation.$resource_status != ResourceStatus.loaded) {
                continue
            }

            if (project_id && (project_id.trim() != "")) {
                const project: IProject = invitation.project as IProject;
                if (project.id != project_id) {
                    continue
                }
            }
            if (invitation.status == status || status == InvitationStatus.none) {
                if (search_term.trim() != "") {
                    const project: IProject = invitation.project as IProject;
                    const contractor: IUser = invitation.invitee as IUser;

                    if (project.name!.toLowerCase().includes(search_term.toLowerCase()) || contractor.username!.toLowerCase().includes(search_term.toLowerCase())) {
                        response.push(invitation)
                    }
                    continue
                }
                response.push(invitation)
            }
        }
        return response;
    };

    const filter_projects = function (status: ProjectStatus, search_term: string = ""): IProject[] {

        let response: IProject[] = [];
        for (const project of Array.from(self.projects.values())) {
            if (project.$resource_status != ResourceStatus.loaded) {
                continue
            }
            if (status != ProjectStatus.none) {
                if (project.status != status) {
                    continue
                }
            }
            if (search_term.trim() != "") {
                if (project.name!.toLowerCase().includes(search_term.toLowerCase())) {
                    response.push(project)
                }
                continue
            }
            response.push(project)
        }

        return response;
    };
    const get_work_diary = function (date: Date, contract_id: string) {
        let dict_ret = new Map<number, any>();
        let ret: any = [];
        for (const log of Array.from(self.logs.values())) {
            const contract: IContract = log.contract as IContract;
            if (contract && contract.id != contract_id) {
                continue
            }

            if (moment(date).format('YYYYMMDD') != moment(log.created_at).format('YYYYMMDD')) {
                continue
            }
            if (!dict_ret.has(log.created_at!.getHours())) {
                dict_ret.set(log.created_at!.getHours(), {
                    hour: log.created_at,
                    logs: [null, null, null, null, null, null]
                })
            }

            let item = dict_ret.get(log.created_at!.getHours());
            item.logs[Math.floor(log.created_at!.getMinutes() / 10)] = log;
        }
        let keys = Array.from(dict_ret.keys());
        for (const k of keys.sort((a, b) => a - b)) {
            ret.push(dict_ret.get(k))
        }
        return ret;
    };
    const projects_as_options = function (): { key: string, text: string, value: string }[] {
        let response = [];
        for (const project of Array.from(self.projects.values())) {
            response.push({key: project.id, text: project.name!, value: project.id})
        }
        return response;
    };

    return {filter_contracts, filter_projects, filter_invitations, get_work_diary, projects_as_options}
}).actions(self => {

    const add_project = (project: IProject) => {
        if (project.owner) {
            const owner: string = project.owner as string;
            if (!self.users.has(owner)) {
                add_user({id: owner})
            }
        }
        self.projects.set(project.id, project)
    };

    const load_projects = flow(function* () {
        if (self.list_project_date) {
            if (moment(new Date()).diff(moment(self.list_project_date), "seconds") < self.list_interval) {
                return null;
            }
        }
        self.list_project_date = new Date();

        try {
            const projects: IApiProject[] = yield Api.projects.list();
            for (const project of projects) {
                add_project({
                    id: `${project.id}`,
                    owner: `${project.owner_id}`,
                    name: project.title,
                    description: project.description,
                    created_at: moment(project.created_date_time).toDate(),
                    $resource_status: ResourceStatus.loaded
                });
            }
        } catch (e) {
            console.error(e)
        }
    });

    const load_invitations = flow(function* () {
        if (self.list_invitation_date) {
            if (moment(new Date()).diff(moment(self.list_invitation_date), "seconds") < self.list_interval) {
                return null

            }
        }
        self.list_invitation_date = new Date();

        try {
            const invitations: IApiInvitation[] = yield Api.invitations.list();
            for (const invitation of invitations) {
                add_invitation({
                    id: `${invitation.id}`,
                    created_at: moment(invitation.created_date_time).toDate(),
                    status: invitation.status,
                    project: `${invitation.project_id}`,
                    invitee: `${invitation.invitee_id}`,
                    terms: `${invitation.terms_id}`,
                    $resource_status: ResourceStatus.loaded
                });
                if (self.user && self.user.kind == UserKind.contractor) {
                    try {
                        const project: IApiProject = yield  Api.projects.get(`${invitation.project_id}`);
                        add_project({
                            id: `${project.id}`,
                            owner: `${project.owner_id}`,
                            name: project.title,
                            description: project.description,
                            created_at: moment(project.created_date_time).toDate(),
                            $resource_status: ResourceStatus.loaded
                        });

                    } catch (e) {
                        console.error(e)
                    }
                }
            }
        } catch (e) {
            console.error(e)
        }
    });

    const add_contract = (input: IContract) => {
        if (input.project) {
            const project = input.project as string;
            if (!self.projects.has(project)) {
                add_project({id: project})
            }
        }

        if (input.contractor) {
            const contractor = input.contractor as string;

            if (!self.users.has(contractor)) {
                add_user({id: contractor})
            }
        }

        let local_contract = self.contracts.get(input.id);
        if (local_contract) {
            input.terms = local_contract.terms;
            input.totals = local_contract.totals;
        }

        self.contracts.set(input.id, input)
    };

    const load_contracts = flow(function* () {
        if (self.list_contract_date) {
            if (moment(new Date()).diff(moment(self.list_contract_date), "seconds") < self.list_interval) {
                return null
            }
        }
        self.list_contract_date = new Date();

        try {
            const contracts: IApiContract[] = yield Api.contracts.list();
            for (const contract of contracts) {
                add_contract({
                    id: `${contract.id}`,
                    project: `${contract.project_id}`,
                    contractor: `${contract.contractor_id}`,
                    started_at: moment(contract.created_date_time).toDate(),
                    status: contract.status,
                    $resource_status: ResourceStatus.loaded
                });
                if (self.user && self.user.kind == UserKind.contractor) {
                    try {
                        const project: IApiProject = yield  Api.projects.get(`${contract.project_id}`);
                        add_project({
                            id: `${project.id}`,
                            owner: `${project.owner_id}`,
                            name: project.title,
                            description: project.description,
                            created_at: moment(project.created_date_time).toDate(),
                            $resource_status: ResourceStatus.loaded
                        });

                    } catch (e) {
                        console.error(e)
                    }
                }

            }

        } catch (e) {
            console.error(e)
        }
    });


    const reload = flow(function* (id: string) {
        self.logs.clear();
        self.list_project_date = null;
        self.list_contract_date = null;
        self.list_invitation_date = null;
        self.invitations.clear();
        self.contracts.clear();
        self.projects.clear();
        self.users.clear();
        self.user = null;

        try {
            const user: IApiUser = yield Api.users.get(id);
            add_user({
                id: user.id,
                username: user.id,
                kind: user.user_type == "work" ? UserKind.contractor : UserKind.client,
                email: user.email as string,
                is_admin: user.is_admin,
                created_date_time: moment(user.created_date_time).toDate(),
                $resource_status: ResourceStatus.loaded,
            });
            self.user = id;
        } catch (e) {
            console.error(e);
        }
        yield load_projects();
        yield load_contracts();
        yield load_invitations();
    });

    const add_user = (input: IUser) => {
        input.username = input.id;
        self.users.set(input.id, input);
    };
    const add_term = (input: ITerm) => {
        self.terms.set(input.id, input);
    };

    const add_invitation = (input: IInvitation) => {
        if (input.invitee) {
            const invitee: string = input.invitee as string;
            if (!self.users.has(invitee)) {
                add_user({id: invitee});
            }
        }

        if (input.terms) {
            const terms: string = input.terms as string;

            if (!self.terms.has(terms)) {
                add_term({id: terms});
            }
        }
        if (input.project) {
            const project: string = input.project as string;

            if (!self.projects.has(project)) {
                add_project({id: project});
            }
        }


        self.invitations.set(input.id, input);
    };

    const delete_invitation = function (id: string) {
        self.invitations.delete(id)
    };
    const add_log = function (input: ILog) {
        self.logs.set(input.id, input)

    };

    const load_logs = flow(function* (contract_id: string, date: Date) {
        let logs_array: Array<ILog> = [];
        //generate_logs(date, contract_id, logs_array);

        try {
            const events: IApiEvent[] = yield Api.contracts.events(contract_id, moment(date).format("YYYY-MM-DD"), moment(date).format("YYYY-MM-DD"));
            for (const event of events) {
                logs_array.push({
                    id: `${event.id}`,
                    screen_shot: event.screenshot_url,
                    created_at: moment(event.created_date_time).toDate(),
                    task_title: event.title,
                    contract: `${event.contract_id}`,
                    keyboard_count: event.keyboard_events_count,
                    mouse_count: event.mouse_events_count,
                })
            }
        } catch (e) {
            console.error(e)
        }

        logs_array = logs_array.sort(function (a, b) {
            return a.created_at!.getTime() - b.created_at!.getTime()
        });

        for (let i = 0; i < logs_array.length; i++) {
            if (i > 0) {
                logs_array[i].previous_log_id = logs_array[i - 1].id
            }
            if (i < (logs_array.length - 1)) {
                logs_array[i].next_log_id = logs_array[i + 1].id
            }
        }

        for (const lg of logs_array) {
            add_log({
                id: lg.id,
                screen_shot: lg.screen_shot,
                created_at: lg.created_at,
                task_title: lg.task_title,
                contract: contract_id,
                keyboard_count: lg.keyboard_count,
                mouse_count: lg.mouse_count,
                previous_log_id: lg.previous_log_id || null,
                next_log_id: lg.next_log_id || null,

            })
        }
        return null
    });

    return {
        reload,
        add_contract,
        add_project,
        delete_invitation,
        add_invitation,
        load_projects,
        add_user,
        load_contracts,
        load_invitations,
        load_logs
    }
});

export const Store = Root.create({});
Api.set_reload(Store.reload);

//check if credentials are stored in localStorage
if (window && window.localStorage) {
    const id = window.localStorage.getItem("user_id");
    const token = window.localStorage.getItem("token");
    const email = window.localStorage.getItem("email");
    if (email && token && id) {
        Api.login(email, id, token, false)
    }
}


