import * as React from "react";
import {inject} from 'mobx-react';
import {RouterView} from 'mobx-state-router';
import {Contracts} from "./views/contracts";
import {SingleContract} from "./views/single-contract";
import {MainLayout} from "./views/main_layout";
import {Login} from "./views/login";
import {AccountLayout} from "./views/account_layout";
import {Register} from "./views/register";
import {Projects} from "./views/projects";
import {SingleProject} from "./views/single-project";
import {ScreenShot} from "./views/contract/screenshot";

const viewMap = {
    contracts: <MainLayout><Contracts/></MainLayout>,
    contract: <MainLayout><SingleContract/></MainLayout>,
    login: <AccountLayout><Login/></AccountLayout>,
    register: <AccountLayout><Register/></AccountLayout>,
    projects: <MainLayout><Projects/></MainLayout>,
    project: <MainLayout><SingleProject/></MainLayout>,
    screenshots: <ScreenShot/>,


};


class ShellBase extends React.Component<any, any> {
    render() {
        const routerStore = this.props.rootStore.routerStore;

        return (
            <div>
                <RouterView routerStore={routerStore} viewMap={viewMap}/>
            </div>
        );
    }
}

export const Shell = inject('rootStore')(ShellBase);