import * as React from "react";
import {Button, Header, Image, Modal, Form, Segment, Icon, Message, TextArea} from 'semantic-ui-react'
import {observer} from "mobx-react";
import {rootStore} from "../stores";
import {Api, TrackerError} from "../../api";
import {Store} from "../../models";
import {ResourceStatus} from "../../enumerations";
import * as moment from 'moment';
import {removeModal} from "../../utils";


@observer
export class EndContract extends React.Component <any, any> {


    render() {
        const queryParams: any = rootStore.routerStore.routerState.queryParams;

        return (<Modal
            open={queryParams.modal == "end_contract"}
            size={"small"}
            dimmer={"blurring"}>
            <Modal.Header>End Contract</Modal.Header>
            <Modal.Content>
                <p>Are you sure you want to end the contract</p>
            </Modal.Content>
            <Modal.Actions>
                <Button negative onClick={() => {
                    rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                        rootStore.routerStore.routerState.params,
                        removeModal(rootStore.routerStore.routerState.queryParams))
                }}>No</Button>
                <Button
                    onClick={() => {
                        Api.contracts.end(`${queryParams.modal_contract_id}`).then(() => {
                            const contract = Store.contracts.get(queryParams.modal_contract_id);
                            if (contract) {
                                contract.end!();
                            }
                            rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                rootStore.routerStore.routerState.params,
                                removeModal(rootStore.routerStore.routerState.queryParams))
                        });

                    }}
                        positive
                        icon='checkmark'
                        labelPosition='right'
                        content='Yes'/>
                        </Modal.Actions>
                        </Modal>)
                    }
                }