import * as React from "react";
import {observer} from "mobx-react";
import {Icon, Button, Segment, Popup, Container} from 'semantic-ui-react'
import * as moment from 'moment';
import {Store} from "../../../models";
import {rootStore} from "../../stores";
import {IContract} from "../../../models/interfaces";


@observer
export class ScreenShot extends React.Component <any, any> {

    constructor(props: any) {
        super(props);
    }

    render() {
        const params: any = rootStore.routerStore.routerState.queryParams;
        const log = Store.logs.get(params.shot_id);
        const contract: IContract = log && log.contract as IContract || {id: ""};
        return (
            <Container fluid>
                <Segment basic className={"project-header"}>
                    <div className={"project-header-left"}>
                        <Button icon
                                onClick={() => {
                                    rootStore.routerStore.goTo("contract",
                                        {contract_id: contract && contract.id, view: "work_diary"},
                                        {date: moment(log && log.created_at).format("YYYYMMDD")})
                                }}
                                labelPosition={"left"}
                                color={"grey"}>
                            WORK DIARY
                            <Icon name={"arrow left"}/>

                        </Button>
                    </div>
                    <div className={"project-header-right"}>
                        <Button
                            onClick={() => {
                                rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                    rootStore.routerStore.routerState.params,
                                    {
                                        ...rootStore.routerStore.routerState.queryParams,
                                        shot_id: log && log.previous_log_id || ""
                                    })
                            }}
                            disabled={!log || !log.previous_log_id}
                            primary>
                            PREVIOUS
                        </Button>
                        <Button disabled>
                            {moment(log && log.created_at).format("ddd, MMMM D, YYYY HH:mm a")}
                        </Button>
                        <Button icon
                                onClick={() => {
                                    rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                        rootStore.routerStore.routerState.params,
                                        {
                                            ...rootStore.routerStore.routerState.queryParams,
                                            shot_id: log && log.next_log_id || ""
                                        })

                                }}
                                disabled={!log || !log.next_log_id}
                                primary>
                            NEXT
                        </Button>
                    </div>

                </Segment>
                <Segment basic>
                    <img src={log && log.screen_shot} style={{width: "100%"}}/>
                </Segment>

            </Container>
        )
    }

}


