import * as React from "react";
import {observer} from "mobx-react";
import {
    Container,
    Dropdown,
    Segment,
    Header,
    Icon,
    Button,
    Label,
} from 'semantic-ui-react'
import {Store} from "../../models"
import {rootStore} from "../stores";
import {ContractState, InvitationStatus, ProjectStatus, UserKind} from "../../enumerations";
import {RouterLink} from "mobx-state-router";
import {Api} from "../../api";

const ReactMarkdown = require('react-markdown')


@observer
export class SingleProject extends React.Component <any, any> {
    changeTab = (e: any, data: any) => {
        const params: any = rootStore.routerStore.routerState.params;
        rootStore.routerStore.goTo("project", {project_id: params.project_id, view: data.name});
    };

    render() {
        const params: any = rootStore.routerStore.routerState.params;
        const queryParams: any = rootStore.routerStore.routerState.queryParams;
        const project = Store.projects.get(params.project_id);
        const view = params.view || "description";
        const contract_label = (<Label as={"a"}>
            <Icon name='address card'/>
            {Store.filter_contracts(ContractState.none, "", project && project.id).length + Store.filter_invitations(InvitationStatus.none, "", project && project.id).length}
            <Label.Detail>Contracts</Label.Detail>
        </Label>);

        return (
            <Container className={"project-list"}>
                <Segment basic className={"project-header"}>
                    <div className={"project-header-left"}>
                        <Header as={"h2"} style={{textTransform: "capitalize"}}>{project && project!.name}</Header>
                    </div>
                    <div className={"project-header-right"}>
                    </div>
                </Segment>

                <Segment basic className={"project-header"}>
                    <div className={"project-header-left"}>
                        {Store.user && Store.user.kind == UserKind.client ?
                            <Dropdown trigger={contract_label} pointing='top left' icon={null}>
                                <Dropdown.Menu>
                                    <Dropdown.Item onClick={() => {
                                        rootStore.routerStore.goTo("contracts",
                                            {},
                                            {
                                                project_id: project && project.id,
                                                status: "contract_started"
                                            }
                                        );
                                    }}
                                    >
                                        <Icon name={"circle"} color={"blue"}/>
                                        Active({Store.filter_contracts(ContractState.started, "", project && project.id).length})
                                    </Dropdown.Item>
                                    <Dropdown.Item onClick={() => {
                                        rootStore.routerStore.goTo("contracts",
                                            {},
                                            {
                                                project_id: project && project.id,
                                                status: "invitation_pending"
                                            });
                                    }}
                                    >
                                        <Icon name={"circle"} color={"orange"}/>
                                        Sent
                                        Invite({Store.filter_invitations(InvitationStatus.none, "", project && project.id).length})
                                    </Dropdown.Item>
                                    <Dropdown.Item onClick={() => {
                                        rootStore.routerStore.goTo("contracts",
                                            {},
                                            {
                                                project_id: project && project.id,
                                                status: "contract_paused"
                                            });
                                    }}
                                    >
                                        <Icon name={"circle"} color={"grey"}/>
                                        Paused({Store.filter_contracts(ContractState.paused, "", project && project.id).length})
                                    </Dropdown.Item>
                                    <Dropdown.Item onClick={() => {
                                        rootStore.routerStore.goTo("contracts",
                                            {},
                                            {
                                                project_id: project && project.id,
                                                status: "contract_ended"
                                            });
                                    }}
                                    >
                                        <Icon name={"circle"} color={"black"}/>
                                        Ended({Store.filter_contracts(ContractState.ended, "", project && project.id).length})
                                    </Dropdown.Item>
                                </Dropdown.Menu>
                            </Dropdown>

                            : null}
                    </div>
                    <div className={"project-header-right"}>
                        <Button icon
                                labelPosition='left'
                                size={"tiny"}
                                color={"grey"}
                                onClick={() => {
                                    if (Store.user && Store.user.kind == UserKind.client) {
                                        rootStore.routerStore.goTo("projects",
                                            {},
                                            {
                                                status: ProjectStatus.active
                                            });
                                    } else {
                                        rootStore.routerStore.goTo("contracts",
                                            {},
                                            {
                                                status: "invitation_pending"
                                            });
                                    }

                                }}
                        >
                            <Icon name={"arrow left"}/>
                            BACK
                        </Button>
                        {Store.user && Store.user.kind == UserKind.client ?
                            <Button icon
                                    labelPosition='left'
                                    size={"tiny"}
                                    onClick={() => {
                                        rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName,
                                            params,
                                            {
                                                ...queryParams,
                                                modal: "create_invitation",
                                                modal_project_id: params.project_id
                                            });
                                    }}
                                    primary>
                                <Icon name={"paper plane"}/>
                                INVITE USER
                            </Button> : null}
                        {(Store.user && Store.user.kind == UserKind.contractor) && (Store.filter_invitations(InvitationStatus.pending, "", project && project.id).length > 0) ?
                            <Button icon
                                    labelPosition='left'
                                    size={"tiny"}
                                    onClick={() => {
                                        const invitation = Store.filter_invitations(InvitationStatus.pending, "", project && project.id)[0];
                                        Api.invitations.accept(invitation.id).then(() => {
                                                Store.delete_invitation(invitation.id);
                                            }
                                        )
                                    }}
                                    primary>
                                <Icon name={"check"}/>
                                ACCEPT INVITE
                            </Button> : null}

                        {(Store.user && Store.user.kind == UserKind.contractor) && (Store.filter_invitations(InvitationStatus.pending, "", project && project.id).length > 0) ?
                            <Button icon
                                    labelPosition='left'
                                    size={"tiny"}
                                    onClick={() => {
                                        const invitation = Store.filter_invitations(InvitationStatus.pending, "", project && project.id)[0];
                                        Api.invitations.decline(invitation.id).then(() => {
                                                Store.delete_invitation(invitation.id);
                                            }
                                        )
                                    }}
                                    negative>
                                <Icon name={"close"}/>
                                DECLINE INVITE
                            </Button> : null}

                        {(Store.user && Store.user.kind == UserKind.contractor) && (Store.filter_contracts(ContractState.started, "", project && project.id).length > 0) ?
                            <Button icon
                                    labelPosition='left'
                                    size={"tiny"}
                                    onClick={() => {
                                        const contract = Store.filter_contracts(ContractState.started, "", project && project.id)[0];
                                        rootStore.routerStore.goTo("contract", {contract_id:contract.id, view: "time"})
                                    }}
                                    primary>
                                <Icon name={"address card"}/>
                                View Contract
                            </Button> : null}
                    </div>

                </Segment>
                <Segment basic style={{padding: 0}}>
                    <div className={"table-filters"}>
                        <div className={"filter"}>
                            <RouterLink
                                className={params.view == "description" ? "filter-active" : ""}
                                routeName={"project"}
                                params={{...params, view: "description"}}
                                queryParams={{...queryParams}}>
                                Description
                            </RouterLink>
                        </div>
                    </div>

                </Segment>
                {view == "description" ?
                    <Segment basic>
                        <ReactMarkdown source={project && project!.description || ""}/>
                    </Segment> : null}

            </Container>
        )
    }

}


