import {types, flow, IModelType} from "mobx-state-tree"
import {User} from "./users";
import {Project} from "./projects";
import {mapCurrency} from "../utils";
import {ContractState, RateCurrency, ResourceStatus} from "../enumerations";
import {IContract, IContractTotal, IContractWeekReduce, ITerm} from "./interfaces";
import {Api, IApiTerm, IApiTotals} from "../api";

/*
    Contract models
*/

export const ContractTerm: IModelType<Partial<ITerm>, ITerm> = types.model({
    weekly_limit: types.optional(types.number, 0),
    rate: types.optional(types.number, 0),
    rate_currency: types.optional(types.enumeration("RateCurrency", [
        RateCurrency.none,
        RateCurrency.dollar,
        RateCurrency.euro]), RateCurrency.none),
});

export const ContractCurrentWeek: IModelType<Partial<IContractWeekReduce>, IContractWeekReduce> = types.model({
    hours_logged: types.optional(types.number, 0),
});

export const ContractTotal: IModelType<Partial<IContractTotal>, IContractTotal> = types.model({
    last_24_hours: types.optional(types.number, 0),
    current_week: types.optional(types.number, 0),
    previous_week: types.optional(types.number, 0),
    since_start: types.optional(types.number, 0),
    last_event_time: types.maybe(types.Date)
});

export const Contract: IModelType<Partial<IContract>, IContract> = types.model({
    id: types.identifier(types.string),
    project: types.maybe(types.reference(Project)),
    contractor: types.maybe(types.reference(User)),
    started_at: types.Date,
    terms: types.maybe(ContractTerm),
    current_week: types.maybe(ContractCurrentWeek),
    totals: types.maybe(ContractTotal),
    status: types.optional(types.enumeration("ContractState", [
        ContractState.none,
        ContractState.paused,
        ContractState.started,
        ContractState.ended,
        ContractState.not_started]), ContractState.none),
    $resource_status: types.optional(types.enumeration('ResourceStatus', [
            ResourceStatus.not_found,
            ResourceStatus.none,
            ResourceStatus.loaded,
            ResourceStatus.forbidden,
            ResourceStatus.error]),
        ResourceStatus.none)
}).actions(self => {
    const end = function () {
        self.status = ContractState.ended
    };
    const resume = function () {
        self.status = ContractState.started
    };
    const pause = function () {
        self.status = ContractState.paused
    };
    const afterCreate = flow(function* () {
        try {
            const totals: IApiTotals = yield Api.contracts.totals(self.id);
            self.totals = ContractTotal.create({
                last_24_hours: totals.last_24_hours,
                current_week: totals.current_week,
                previous_week: totals.previous_week,
                since_start: totals.since_start,
            });
        } catch (e) {
            console.error(e);
        }
        try {
            const terms: IApiTerm = yield Api.contracts.terms(self.id);
            self.terms = ContractTerm.create({
                weekly_limit: terms.weekly_limit_in_hours,
                rate: terms.price_per_hour,
                rate_currency: terms.currency_id == "eur" ? RateCurrency.euro : RateCurrency.dollar
            });
        } catch (e) {
            console.error(e);
        }
        yield Promise.resolve();
    });
    return {end, resume, pause, afterCreate}
});
