import {types, IModelType, flow} from "mobx-state-tree"
import {User} from "./users";
import {Project} from "./projects";
import {ContractTerm} from "./contracts";
import {IInvitation} from "./interfaces";
import {InvitationStatus, ResourceStatus} from "../enumerations";

/*
    Invitation models
*/


export const Invitation: IModelType<Partial<IInvitation>, IInvitation> = types.model({
    id: types.identifier(types.string),
    created_at: types.optional(types.Date, new Date()),
    status: types.optional(types.enumeration("InvitationStatus", [InvitationStatus.none,
        InvitationStatus.accepted,
        InvitationStatus.declined,
        InvitationStatus.pending]), InvitationStatus.none),
    project: types.maybe(types.reference(Project)),
    invitee: types.maybe(types.reference(User)),
    terms: types.maybe(types.reference(ContractTerm)),
    $resource_status: types.optional(types.enumeration('ResourceStatus', [
            ResourceStatus.not_found,
            ResourceStatus.none,
            ResourceStatus.loaded,
            ResourceStatus.forbidden,
            ResourceStatus.error]),
        ResourceStatus.none)
});