import {RouterStore} from "mobx-state-router";
import {Store} from "../models";
import {rootStore} from "./stores";
import {ContractState, ResourceStatus} from "../enumerations";
import {Api} from "../api";
import * as moment from 'moment';

export const routes = [
    {
        name: 'home',
        pattern: '/',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            rootStore.routerStore.goTo("contracts", {}, {status: `contract_${ContractState.started}`});
            return Promise.reject(null);
        }
    },
    {
        name: 'login',
        pattern: '/login',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            if (!Api.is_logged()) {
                return Promise.resolve();
            }

            routerStore.goTo("projects");
            return Promise.reject();
        }
    },
    {
        name: 'register',
        pattern: '/register',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            if (!Api.is_logged()) {
                return Promise.resolve();
            }

            routerStore.goTo("projects");
            return Promise.reject();
        }
    },
    {
        name: 'contracts',
        pattern: '/contracts',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            if (Api.is_logged()) {
                Store.load_projects();
                Store.load_contracts();
                Store.load_invitations();
                return Promise.resolve();
            }
            routerStore.goTo("login");
            return Promise.reject();
        }
    },
    {
        name: 'contract',
        pattern: '/contracts/:contract_id/:view',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            if (Api.is_logged()) {
                Store.load_projects();
                Store.load_contracts().then(() => {
                    Store.load_logs(toState.params.contract_id,
                        moment(toState.queryParams.date || moment().format("YYYYMMDD"), "YYYYMMDD").toDate());

                });
                Store.load_invitations();
                return Promise.resolve();
            }
            routerStore.goTo("login");
            return Promise.reject();
        }
    },
    {
        name: 'projects',
        pattern: '/projects',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            if (Api.is_logged()) {
                Store.load_projects();
                Store.load_contracts();
                Store.load_invitations();
                return Promise.resolve();
            }
            routerStore.goTo("login");
            return Promise.reject();

        }
    },
    {
        name: 'project',
        pattern: '/project/:project_id/:view',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            if (Api.is_logged()) {
                Store.load_projects();
                Store.load_contracts();
                Store.load_invitations();
                if (!Store.projects.has(toState.params.project_id)) {
                    Api.projects.get(toState.params.project_id).then((response) => {
                        Store.add_project({
                            id: `${response.id}`,
                            owner: `${response.owner_id}`,
                            name: response.title,
                            description: response.description,
                            created_at: moment(response.created_date_time).toDate(),
                            $resource_status: ResourceStatus.loaded
                        });
                    }).catch((e) => {
                        Store.add_project({
                            id: `${toState.params.project_id}`,
                            $resource_status: ResourceStatus.not_found
                        });
                    })
                }
                return Promise.resolve();
            }

            routerStore.goTo("login");
            return Promise.reject();
        }
    },
    {
        name: 'screenshots',
        pattern: '/screenshots/:shot_id',
        onEnter: (fromState: any, toState: any, routerStore: RouterStore) => {
            if (Api.is_logged()) {
                if (Store.logs.has(toState.params.shot_id)) {
                    return Promise.resolve();

                }
                else {
                    rootStore.routerStore.goTo("contracts", {}, {status: `contract_${ContractState.started}`});
                    return Promise.reject();
                }

            }

            routerStore.goTo("login");
            return Promise.reject();
        }
    },

    {
        name: 'notFound',
        pattern: '/not-found'
    }

];