import * as React from "react";
import {observer} from "mobx-react";
import {
    Segment,
    Header,
    Form,
    Icon,
    Message,
    Button,
} from 'semantic-ui-react'
import {RouterLink} from 'mobx-state-router';
import {ContractState, UserKind} from "../../enumerations";
import {Api, TrackerError} from "../../api";
import {rootStore} from "../stores";


@observer
export class Register extends React.Component <any, any> {
    state = {
        form: {
            email: "",
            username: "",
            kind: UserKind.none,
            password: "",
            confirm_password: "",
            error: null,
            last_name: "",
            first_name: "",
            loading: false
        }
    };

    render() {
        return (
            <Segment>
                <Segment vertical>
                    <Header>Register new user</Header>
                </Segment>
                <Segment basic>
                    <Form loading={this.state.form.loading} className={"login-form"} onSubmit={(e) => {
                        e.preventDefault();

                        if (this.state.form.password != this.state.form.confirm_password) {
                            return
                        }
                        this.setState({
                            form: {
                                ...this.state.form,
                                loading: true
                            }
                        });
                        Api.users.register(this.state.form.email,
                            this.state.form.username,
                            this.state.form.kind == "client" ? "hire" : "work",
                            this.state.form.password,
                            this.state.form.first_name,
                            this.state.form.last_name).then((response) => {
                            Api.login(response.email, response.id, response.token);
                            this.setState({
                                form: {
                                    ...this.state.form,
                                    loading: false
                                }
                            });
                            rootStore.routerStore.goTo("contracts", {}, {status: `contract_${ContractState.started}`});

                        }).catch((error: TrackerError) => {
                            this.setState({
                                form: {
                                    ...this.state.form,
                                    error: error.message,
                                    loading: false
                                }
                            })
                        });

                    }}>
                        <Form.Input label='Email address'
                                    placeholder='Enter email'
                                    type="email"
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                email: data.value
                                            }
                                        })
                                    }} value={this.state.form.email}
                                    required/>
                        <Form.Input label='Username'
                                    placeholder='Enter username'
                                    type="text"
                                    name="user"
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                username: data.value
                                            }
                                        })
                                    }} value={this.state.form.username}
                                    required/>
                        <Form.Input label='First name'
                                    placeholder='First name'
                                    type="text"
                                    name="user"
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                first_name: data.value
                                            }
                                        })
                                    }} value={this.state.form.first_name}
                                    required/>
                        <Form.Input label='Last name'
                                    placeholder='Last name'
                                    type="text"
                                    name="user"
                                    onChange={(e: any, data: any) => {
                                        this.setState({
                                            form: {
                                                ...this.state.form,
                                                last_name: data.value
                                            }
                                        })
                                    }} value={this.state.form.last_name}
                                    required/>
                        <Form.Select label='Account type'
                                     options={[
                                         {key: UserKind.contractor, text: "Contractor", value: UserKind.contractor},
                                         {key: UserKind.client, text: "Client", value: UserKind.client},

                                     ]}
                                     placeholder='Account type'
                                     onChange={(e: any, data: any) => {
                                         this.setState({
                                             form: {
                                                 ...this.state.form,
                                                 kind: data.value,
                                             }
                                         })
                                     }}
                                     value={this.state.form.kind}
                                     required/>
                        <Segment attached>
                            <Form.Input label='Password'
                                        placeholder='Password'
                                        name={"password"}
                                        type="password"
                                        onChange={(e: any, data: any) => {
                                            this.setState({
                                                form: {
                                                    ...this.state.form,
                                                    password: data.value
                                                }
                                            })
                                        }} value={this.state.form.password}
                                        required/>
                            <Form.Input label='Confirm password'
                                        placeholder='Password'
                                        type="password"
                                        name={"confirm_password"}
                                        onChange={(e: any, data: any) => {
                                            this.setState({
                                                form: {
                                                    ...this.state.form,
                                                    confirm_password: data.value
                                                }
                                            })
                                        }} value={this.state.form.confirm_password}
                                        required/>
                        </Segment>
                        {this.state.form.password == this.state.form.confirm_password ?
                            <Message warning attached='bottom' visible>
                                <Icon name='warning'/>
                                Please use a password which is strong enough.
                            </Message> : <Message negative attached='bottom' visible>
                                <Icon name='exclamation'/>
                                Passwords do not match.
                            </Message>
                        }

                        Already registered? Let's <RouterLink routeName={"login"}>sign in</RouterLink>

                        {this.state.form.error ? <Message color='red'>{this.state.form.error}</Message> : null}

                        <div className={"login-form-footer"}>
                            <Button size={"big"} negative loading={this.state.form.loading}>Register</Button>
                        </div>
                    </Form>
                </Segment>
            </Segment>
        )
    }

}


