import {types, flow, IModelType} from "mobx-state-tree"
import {User} from "./users";
import {IProject} from "./interfaces";
import {ProjectStatus, ResourceStatus} from "../enumerations";


/*
    Project models
 */

export const Project: IModelType<Partial<IProject>, IProject> = types.model({
    id: types.identifier(types.string),
    name: types.optional(types.string, ""),
    description: types.optional(types.string, ""),
    created_at: types.optional(types.Date, new Date()),
    owner: types.maybe(types.reference(User)),
    status: types.optional(types.enumeration('ProjectStatus', [
            ProjectStatus.active,
            ProjectStatus.archived]),
        ProjectStatus.active),
    $resource_status: types.optional(types.enumeration('ResourceStatus', [
            ResourceStatus.not_found,
            ResourceStatus.none,
            ResourceStatus.loaded,
            ResourceStatus.forbidden,
            ResourceStatus.error]),
        ResourceStatus.none),

});