import * as React from "react";
import {Button, Header, Image, Modal, Form, Segment, Icon, Message, TextArea} from 'semantic-ui-react'
import {observer} from "mobx-react";
import {rootStore} from "../stores";
import {Api, TrackerError} from "../../api";
import {Store} from "../../models";
import {ResourceStatus} from "../../enumerations";
import * as moment from 'moment';


@observer
export class CreateProject extends React.Component <any, any> {
    state = {
        form: {
            name: "",
            description: "",
            loading: false,
            error: null
        }
    };

    render() {
        const queryParams: any = rootStore.routerStore.routerState.queryParams;

        return (<Modal
            open={queryParams.modal == "create_project" && rootStore.routerStore.routerState.routeName == "projects"}
            centered={false} dimmer={"blurring"}>
            <Modal.Header>Create Project</Modal.Header>
            <Modal.Content>
                <Form>
                    <Form.Input label='Choose a name for your project'
                                placeholder='Project Name'
                                onChange={(e: any, data: any) => {
                                    this.setState({
                                        form: {
                                            ...this.state.form,
                                            name: data.value
                                        }
                                    })
                                }}
                                value={this.state.form.name}
                                required/>
                    <div className={"required field"} style={{marginBottom: 0, marginTop: 16}}>
                        <label>Describe here your project </label>
                        <TextArea autoHeight
                                  placeholder='Description ...'
                                  required
                                  onChange={(e: any, data: any) => {
                                      this.setState({
                                          form: {
                                              ...this.state.form,
                                              description: data.value
                                          }
                                      })
                                  }}
                                  value={this.state.form.description}
                                  style={{minHeight: 200, borderRadius: 0, maxHeight: 400}}/>
                    </div>
                    <Message info attached='bottom' visible>
                        <Icon name='info'/>
                        Markdown supported.
                    </Message>
                </Form>
                {this.state.form.error ? <Message color='red'>{this.state.form.error}</Message> : null}
            </Modal.Content>
            <Modal.Actions>
                <Button color='black' onClick={() => {
                    rootStore.routerStore.goTo(rootStore.routerStore.routerState.routeName)
                }}>
                    Cancel
                </Button>
                <Button
                    positive
                    icon='checkmark'
                    labelPosition='right'
                    content="Add Project"
                    onClick={(e) => {

                        this.setState({
                            form: {
                                ...this.state.form,
                                loading: true
                            }
                        });
                        Api.projects.create(this.state.form.name, this.state.form.description).then((response) => {

                            Store.add_project({
                                id: `${response.id}`,
                                owner: `${response.owner_id}`,
                                name: response.title,
                                description: response.description,
                                created_at: moment(response.created_date_time).toDate(),
                                $resource_status: ResourceStatus.loaded
                            });

                            this.setState({
                                form: {
                                    ...this.state.form,
                                    loading: false
                                }
                            });

                            rootStore.routerStore.goTo("projects", {})

                        }).catch((error: TrackerError) => {
                            this.setState({
                                form: {
                                    ...this.state.form,
                                    error: error.message,
                                    loading: false
                                }
                            })
                        });
                        this.setState({
                            form: {
                                name: "",
                                description: "",
                                loading: false,
                                error: null
                            }
                        })
                    }}
                />
            </Modal.Actions>
        </Modal>)
    }
}